import React, { Fragment,useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import Pagination from 'react-js-pagination'
import Loader from './Loader'
import Slider from 'rc-slider'
import 'rc-slider/assets/index.css';
import MetaData from './MetaData'
import Product from './Product'
import {useDispatch,useSelector} from 'react-redux'
import axios from 'axios';
import { getProducts } from '../actions/productActions'
import { useAlert } from 'react-alert'
import { Cartget } from '../actions/CartAction'
import SliderView from './SliderView'
const {createSliderWithTooltip}= Slider;
const Range=createSliderWithTooltip(Slider.Range)
const Home = () => {
  const dispatch=useDispatch();
const [price, setPrice] = useState([1,100000])
const [sliderImages, setSliderImages] = useState([]);
const [fslide,setFslide]=useState();
  const alert=useAlert();
  const {loading,products,error,productCount,resPerPage}=useSelector(state=>state.products)
  const [category, setCategory] = useState('')
  const[rating,setRating]=useState(0)
  const categories=[
    'Electronics',
    'Cameras',
    'Laptops',
    'Accesories',
    'Headphones',
    'Food',
    'Books',
    'Clothing',
    'Beauty/Health',
    'Sports',
    'Home',
    'Others'
  ]
  const {keyword}=useParams()
const [currentPage, setCurrentPage] = useState(1)
  useEffect(async() => {
    if(error){
     return  alert.error(error);
    }
    const { data } = await axios.get("/api/v1/slider");
   
    setSliderImages(data.slider);
    dispatch(getProducts(keyword,currentPage,price,category,rating))
    
  }, [dispatch,alert,error,keyword,currentPage,price,category,rating])
  
  const firstSlide =  sliderImages.shift();
  console.log(sliderImages);
function setCurrentPageNO(pageNumber){
setCurrentPage(pageNumber)
}

 
    return (
       <div className="HomePage">
         {loading?<Loader/>:(
           <Fragment>
              <MetaData title={'Buy Best products Online'}/>
       
<SliderView/>
      <div className="container container-fluid">
        
           
      <section id="products" className="container mt-5">
   

<div className="row" >
  {
     products.map(product=>(
      <Product key={product._id} product={product} />
      ))
  
}
     
        </div>
    </section>
        </div>
     
        <div className="d-flex justify-content-center mt-5">
          <Pagination
            activePage={currentPage}
            itemsCountPerPage={resPerPage}
            totalItemsCount={productCount}
            onChange={setCurrentPageNO}
            itemClass="page-item"
            linkClass="page-link"
          />
        </div>
        
           </Fragment>
         )} 
     
        </div>
    )
}

export default Home
